<script>
   import targetSpeed from '../../../stores/targetSpeed';
   const min = 20
   const max = 150
   let bubble ;

   const handleChange = (e) => {
      targetSpeed.setSpeed(e.target.value)
   }

   // when the targetSpeed changes, change the bubble the position
   $: {
      const offset = ($targetSpeed - min)/(max-min)*100;
      if (bubble) {
         bubble.style.left = offset + '%'
         bubble.style.animationDuration = 50 / (offset + 10) + 's'
      }
   }

</script>
<div class="target"><span class="goal"><span class="text">Target is</span><span class="value">{$targetSpeed}</span><span class="unit">WPM</span></span><div class="slider"><input on:input="{handleChange}" min="{min}" max="{max}" type="range"><div class="bubble" bind:this="{bubble}"></div><div class="tip">What is your target typing speed ?</div></div></div>
<style src='./targetSlider.styl'>.target {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.target .goal {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.5em;
}
.target .goal .text {
  margin-right: 1rem;
}
.target .goal .value {
  font-size: 2rem;
  margin-right: 0.5rem;
  text-shadow: 0 0 1rem var(--fontcolor);
}
.target:hover .tip {
  opacity: 1;
}
.target .tip {
  opacity: 0;
  transition: 200ms ease;
  position: absolute;
  color: var(--activechar);
  width: 100%;
  text-align: center;
  bottom: 0;
  pointer-events: none;
  padding: 1rem;
  transform: translateY(100%);
  letter-spacing: 0.5em;
  text-transform: uppercase;
}
.slider {
  position: relative;
  margin-top: 0.3rem;
  width: 25rem;
}
.slider::before {
  content: '';
  width: 100%;
  height: 2px;
  position: absolute;
  display: block;
  top: 50%;
  transform: translateY(-50%);
  background: linear-gradient(to left, var(--fontcolor), var(--typedword));
}
input {
  width: 100%;
  opacity: 0;
  cursor: pointer;
}
.bubble {
  position: absolute;
  width: 1.2rem;
  height: 1.2rem;
  background: var(--fontcolor);
  top: 50%;
  pointer-events: none;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px var(--activechar);
  animation: spin 4s linear infinite;
}
@-moz-keyframes spin {
  to {
    transform: translate(-50%, -50%) rotate(1turn);
  }
}
@-webkit-keyframes spin {
  to {
    transform: translate(-50%, -50%) rotate(1turn);
  }
}
@-o-keyframes spin {
  to {
    transform: translate(-50%, -50%) rotate(1turn);
  }
}
@keyframes spin {
  to {
    transform: translate(-50%, -50%) rotate(1turn);
  }
}
</style>