<script>
   // store
   import stats from '../../stores/stats.js'
   import progress from '../../stores/progress.js'
   import typed from '../../stores/typed.js'
   import words from '../../stores/words.js'
   import targetSpeed from '../../stores/targetSpeed'
   import chaseMode from '../../stores/chaseMode'
   // import targetProgress from './stores/targetProgress'
   // motion
   import {tweened} from 'svelte/motion'
   // utils
   import ms from '../../utils/ms.js'
   // components
   import TargetSlider from './TargetSlider/targetSlider.svelte'

   let wordTypeStart = null
   let speed = tweened(0)
   let time = tweened(0)

   // if begining, note starting time
   $: if ($typed.key && $typed.key !== ' ' && $progress.char === 1 && $progress.word === 0) {
      stats.typingStarted()
   }

   // when a word has been typed, update stats
   $: if ($progress.char === $words[$progress.word].length - 1) {
      stats.log()
   }

   $: $speed = $stats.speed
   $: $time = $stats.time

</script>
<div class="stats"><div class="left"><TargetSlider></TargetSlider></div><div class="center"><div class="speed"><div class="value">{ Math.round($speed) }</div><div class="unit">WPM</div></div><div class="word-stats"><span class="count">{$stats.words}</span><span class="info">words typed in</span><span class="count">{ ($time / 1000).toFixed(2) } S</span></div></div><div class="right"><div class="chase-mode" on:click="{chaseMode.toggle}"><div class="text">SHADOOW mode</div><div class="value" class:on="{$chaseMode}">{$chaseMode ? 'on' : 'off'}</div><div class="tip"><p>compete with your target speed</p></div></div></div></div>
<style src='./stats.styl'>.stats {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-content: center;
  margin-bottom: 4rem;
  padding: 1rem;
}
.center {
  text-align: center;
  color: var(--fontcolor);
  position: relative;
  cursor: pointer;
}
.center:hover .word-stats {
  opacity: 1;
}
.center .speed {
  letter-spacing: 0.4em;
}
.center .speed .value {
  font-size: 4rem;
  text-shadow: 0 0 3rem var(--fontcolor);
}
.left,
.right {
  display: grid;
  justify-content: center;
}
.word-stats {
  transition: 200ms ease;
  opacity: 0;
  pointer-events: none;
  font-size: 0.8rem;
  padding-top: 1rem;
  position: absolute;
  width: 600px;
  bottom: 0;
  left: 50%;
  text-transform: uppercase;
  transform: translate(-50%, 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.5em;
}
.word-stats > * + * {
  margin-left: 1rem;
}
.word-stats .count {
  font-size: 1.2rem;
  text-shadow: 0 0 20px var(--fontcolor);
}
.chase-mode {
  text-transform: uppercase;
  letter-spacing: 0.5em;
  justify-content: center;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  width: 25rem;
  padding-bottom: 0.7rem;
  background: linear-gradient(to right, var(--fontcolor), var(--typedword));
  background-size: 100% 2px;
  background-repeat: no-repeat;
  background-position: bottom;
}
.chase-mode:hover .tip {
  opacity: 1;
}
.chase-mode .tip {
  opacity: 0;
  transition: 200ms ease;
  position: absolute;
  color: var(--activechar);
  max-width: 400px;
  text-align: center;
  bottom: 0;
  pointer-events: none;
  padding: 1rem;
  transform: translateY(100%);
}
.chase-mode .value {
  font-size: 2rem;
  margin-left: 10px;
  transition: 200ms ease;
}
.chase-mode .value.on {
  font-weight: bold;
  text-shadow: 0 0 2rem var(--fontcolor);
}
</style>